.cardList{display:grid;width:100%;min-height:calc(100vh - 180px);grid-auto-columns:1fr;grid-template-columns:1fr;grid-template-rows:45px 1fr 41px;gap:0px 0px;grid-template-areas:"filters" "cards" "pagination"}@media screen and (max-width: 767px){.cardList{min-height:calc(100vh - 180px - 225px);grid-template-rows:225px 1fr 41px}}.cardList .filters{display:flex;align-items:center;justify-content:space-between;gap:20px;grid-area:filters;width:100%}@media screen and (min-width: 320px){.cardList .filters div[class~=Select],.cardList .filters div[class~=Range]{width:100%}}@media screen and (min-width: 768px){.cardList .filters div[class~=Select],.cardList .filters div[class~=Range]{width:calc((100% - 60px)/4)}}.cardList .filters div[class~=Select]>span,.cardList .filters div[class~=Range]>span{width:unset}.cardList .filters div[class~=Range__сontainer--open]{width:calc(100% + 2px)}.cardList .filters div[class~=Select--dark],.cardList .filters div[class~=Range--dark],.cardList .filters ul[class~=Select__optionContainer--dark],.cardList .filters div[class~=Range__сontainer--dark]{background-color:#000}@media screen and (max-width: 767px){.cardList .filters{flex-direction:column;gap:15px}}.cardList .filters .input{color:#000;background-color:var(--color-bg-input);border:none}.cardList .filters .input::-webkit-input-placeholder{color:var(--color-placeholder)}.cardList .filters .input::-moz-placeholder{color:var(--color-placeholder)}.cardList .filters .input:-ms-input-placeholder{color:var(--color-placeholder)}.cardList .filters .input::-ms-input-placeholder{color:var(--color-placeholder)}.cardList .filters .input::placeholder{color:var(--color-placeholder)}.cardList .cards{grid-area:cards;display:flex;align-items:flex-start;justify-content:center;flex-wrap:wrap;align-content:flex-start;gap:20px;padding:45px 0px 40px}.cardList .pagination{grid-area:pagination;height:41px;width:100%;display:flex;align-items:center;justify-content:flex-start}@media screen and (max-width: 430px){.cardList .pagination{display:flex;align-items:center;justify-content:center}}.cardList .pagination .wrapper{display:grid;grid-gap:1px;grid-auto-columns:1fr;grid-template-rows:1fr}.cardList .pagination .wrapper .firstPageBtn,.cardList .pagination .wrapper .lastPageBtn,.cardList .pagination .wrapper .prevPageBtn,.cardList .pagination .wrapper .nextPageBtn{display:flex;align-items:center;justify-content:center}.cardList .pagination .wrapper .firstPageBtn{border-radius:6px 0px 0px 6px}.cardList .pagination .wrapper .lastPageBtn{border-radius:0px 6px 6px 0px}.cardList .pagination .wrapper button{width:40px;height:40px;outline:1px solid var(--color-secondary);color:var(--color-text)}@media screen and (max-width: 430px){.cardList .pagination .wrapper button{width:25px;height:25px}}